.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 30px !important;
    margin: auto;
}

.dots {
    display: inline-flex;

    &--animate .dot {
        &.z {
            animation: scale 0.8s 0.2s forwards;
        }

        &.f,
        &.s {
            animation: right 0.5s forwards;
        }

        &.l {
            animation: rightDown 0.4s 0.1s forwards linear,
                drop 2s 0.4s forwards linear;
        }
    }
}

.dot {
    & {
        display: inline-block;
        width: 10px;
        height: 10px;
        background: #fff;
        border-radius: 10px;
        position: relative;
        margin-left: 6px;
    }

    &.z {
        position: absolute;
        transform: scale(0);

        @keyframes scale {
            100% {
                transform: scale(1);
            }
        }
    }

    &.f,
    &.s {
        transform: translateX(0px);

        @keyframes right {
            100% {
                transform: translateX(16px);
            }
        }
    }

    &.t {
        background: transparent;
    }

    .l {
        margin-left: 0;
        position: absolute;
        top: 0;
        left: 0;

        @keyframes rightDown {
            50% {
                top: 4px;
                left: 16px;
            }

            100% {
                top: 12px;
                left: 24px;
            }
        }

        @keyframes drop {
            100% {
                transform: translate(70px, calc(35px + (100vh / 2)));
            }
        }
    }
}
